.App{
  height:100vh;
  width:100vw;
  margin: 0px;
  padding: 0px;
  text-align: center;
  background-color: #E8E8EA;
  color: black;
  overflow-x: hidden;
}

.container-fluid{
  margin: 0px;
  padding: 0px;;
}

.my_row{
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  }
  
.main_user_component::-webkit-scrollbar {
  width: 1%;
}

.main_user_component::-webkit-scrollbar-track {
  background: rgb(8, 8, 8); 
}
 
.main_user_component::-webkit-scrollbar-thumb {
  background: rgba(245, 245, 245,0.6);
}

.main_admin::-webkit-scrollbar {
  width: 1%;
}

.main_admin::-webkit-scrollbar-track {
  background: rgb(8, 8, 8); 
}
 
.main_admin::-webkit-scrollbar-thumb {
  background: rgba(245, 245, 245,0.6);
}

.spinner-grow{
  height: 200px;
  width: 200px;
  position: fixed;
  z-index: 999;
  color: rgb(69, 86, 92);
  left: 40%;
  top: 20%;
}

/* login component */
.login_component{
  padding: 30px;
  text-align: center;
  width: 60%;
  height: 90%;
  margin: 20px auto;
  border: solid black;
  border-radius: 10px;
  background-color: rgb(168, 178, 180);
  background-image: url('components/pic/bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

 .nav{
   color: black;
   font-weight: bold;
   text-decoration-line: none;
 }

 .nav:hover{
 color: red;
 text-decoration-line: underline;
 }

/* register component */
.register_component{
  padding: 30px;
  text-align: center;
  width: 60%;
  height: 90%;
  margin: 20px auto;
  border: solid black;
  border-radius: 10px;
  background-color: rgb(84, 161, 175);
  background-image: url('components/pic/bg2.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

/* for all forms */
form{
  padding: 20px;
}

input[type='text']{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  border: 0;
}

input[type='text']:hover{
  box-shadow: 2px 2px 2px 2px black;
}
input[type='password']{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  border: 0;
}

input[type='password']:hover{
  box-shadow: 2px 2px 2px 2px black;
}

input[type='image']{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  border: 0;
}

input[type='submit']{
  margin: 10px auto;
  padding: 6px 20px;
  display: block;
}

input[type='number']{
  display: block;
  box-sizing: border-box;
  margin: 6px auto;
  border: 0;
}

input[type='number']:hover{
  box-shadow: 2px 2px 2px 2px black;
}

input[type='date']{
  position: relative;
  left: 5px;
  display: inline-block;
  box-sizing: border-box;
  margin: 6px 0;
  border: 0;
  margin-right: 5px;

}

.inputDateUpdate{
  margin: 10px;
  display: inline-block;
  width: 45%;
}

.p_date{
  display: inline-block;
  margin-right: 5px;
  margin-left: 10px;
}

input[type='date']:hover{
  box-shadow: 2px 2px 2px 2px black;
}


/* users page */
.main_user_component{
  height: 100%;
  width: 100%;
  overflow: auto;  
}

.navbar_user{
  position: fixed;
  display: block;
  box-shadow: 2px 2px 8px 2px black;
  width: 100%;
  height: 7%;
  background-color: rgb(116, 126, 109);
  color: whitesmoke;
  z-index: 999;
}

.title_user{
  display: inline-block;
  position: relative;
  left: -10%;
  text-align: left;
  width: 70%;
  margin-top: 0.2%;
  text-decoration-line: underline;
}

.log_out_btn{
  position: relative;
  top: -6%;
  left: 7%;
  width: 10%;
  height: 90%;
}

.main_box_user{
  position: relative;
  top: 10%;
  height: 90%;
  width: 100%;
}

.title_user_box{
  text-align: center;
  text-decoration-line: underline;
}

.card{
  display: inline-block;
  border: solid rgb(0, 0, 0);
  position: relative;
  left: 12%;
  padding: 15px;
  margin-left: 10px;
  margin-top: 25px;
  margin-bottom: 20px;
  font-weight: bold;
  width: 100%!important;
  background-color: rgb(45, 49, 43);
  color: whitesmoke;
  box-shadow: 2px 2px 4px 2px grey;
}

.card-img-top{
  box-shadow: 2px 2px 4px 2px grey;
  height: 200px;
  margin-bottom: 10px;
}

.card:hover{
margin-top: -1px;
box-shadow: 2px 2px 8px 8px grey;
}

.list-group-item{
  background-color: rgb(156, 150, 140);
  text-align: left;
  color: whitesmoke;
}


.box_vication{
  border: solid black;
  display: inline-block;
  width: 100%;
  background-color: rgb(100, 107, 117);
  margin: 10px;
  box-shadow: 2px 2px 2px 2px black;
}


/* admin */
.main_admin{
  height: 100%;
  width: 100%;
  overflow: auto;
}

.components_admin{
  position: relative;
  top: 15%;
  height: 90%;
}


/* navbar admin */
.navbar_admin{
  position: fixed;
  height: 7%;
  width: 100%;
  display: block;
  box-shadow: 2px 2px 8px 2px black;
  background-color: rgb(116, 126, 109);
  color: whitesmoke;
  z-index: 999;
}

.title_admin{
  display: inline-block;
  text-align: left;
  width: 70%;
  margin-top: 0.2%;
  text-decoration-line: underline;
}

.div_navlink{
  display: inline-block;
  height: 100%;
  width: 10%;
  position: relative;
  top: 8%;
}

.btn_u{
  text-align: left;
  position: absolute;
  right: 83%;
  top:-0.3%;
}

.btn_x{
  text-align: left;
  position: absolute;
  left: 83%;
  top:-0.3%;

}

.boxForm{
  z-index: 999;
  position: fixed;
  top: -10%;
  height: 110%;
  width: 100%;
  background-color: rgb(31, 32, 31);

}

/* admin vication */
.btn_add{
  position: fixed;
  right: 25px;
  z-index: 999;
}

.form_add{
  opacity: 1;
  position: fixed;
  left: 50%;
  top: 10%;
  transform: translatex(-50%);
  background-color: #EAEAE8;
  display: block;
  z-index: 999;
  height: 75%;
  width: 77%;
  border: solid black;
  border-radius: 10px;
}

.form_update{
  position: fixed;
  left: 50%;
  top: 10%;
  transform: translatex(-50%);
  background-color: #EAEAE8;
  display: block;
  z-index: 999;
  height: 75%;
  width: 77%;
  border: solid black;
  border-radius: 10px;
}

.btn_x1{
  text-align: left;
  position: relative;
  left: 48.5%;
  border-radius: 5px;
}

.chart{
  height: 100%;
  width: 100%;
  background-color: rgb(54, 46, 46);
  margin-top: -35px;
}